import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export const registrationSuccess = () =>
  toast.success("Registration Success", {
    position: "bottom-right",
    autoClose: 3000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });

